.form__label {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  margin-right: 13px;
}
.form__label--radio {
  position: relative;
  padding-left: 45px;
  padding-top: 6px;
  padding-bottom: 6px;
}
.form__label--radio::before {
  content: "";
  display: block;
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #cdcdcd;
  top: 0;
  left: 0;
}
.form__label--radio::after {
  content: "";
  display: block;
  position: absolute;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #68b738;
  top: 8px;
  left: 8px;
  display: none;
}
.form__label:hover::before {
  border-color: #bababa;
}

.form__radio:checked + .form__label--radio::after {
  display: block;
}

.form__radio:disabled + .form__label--radio {
  opacity: 0.3;
}

.form__radio:disabled + .form__label--radio:hover::before {
  border-color: #cdcdcd;
}

.form__radio:focus-visible + .form__label--radio::before {
  border-color: #444444;
}

.form__label--checkbox {
  position: relative;
  padding-left: 45px;
  padding-top: 6px;
  padding-bottom: 6px;
}
.form__label--checkbox::before {
  content: "";
  display: block;
  position: absolute;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  top: 0;
  left: 0;
}
.form__label--checkbox::after {
  content: "";
  display: block;
  position: absolute;
  width: 26px;
  height: 26px;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  top: 2px;
  left: 2px;
  display: none;
}
.form__label--checkbox:hover::before {
  border-color: #bababa;
}

.form__checkbox:checked + .form__label--checkbox::after {
  display: block;
}

.form__checkbox:disabled + .form__label--checkbox {
  opacity: 0.3;
}

.form__checkbox:disabled + .form__label--checkbox:hover::before {
  border-color: #cdcdcd;
}

.form__checkbox:focus-visible + .form__label--checkbox::before {
  border-color: #444444;
}

.form input[type=number]::-webkit-outer-spin-button,
.form input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}
.form input[type=number],
.form input[type=number]:hover,
.form input[type=number]:focus {
  appearance: textfield;
}

.form__field {
  display: flex;
  align-items: center;
  margin-bottom: 14px;
  position: relative;
}
.form__field:last-child {
  margin-bottom: 0;
}

.form__control {
