.promo {
  text-align: center;
  padding-top: 27px;
  background-image: url("../img/green-cat.jpg"), linear-gradient(to bottom, #64a73c 302px, #ffffff 302px);
  background-repeat: no-repeat;
  background-position: center top;
  padding-bottom: 20px;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/green-cat@2x.jpg"), linear-gradient(to bottom, #64a73c 302px, #ffffff 302px);
    background-size: 320px 302px, auto;
  }
}

.promo__title {
  color: #ffffff;
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  margin-top: 0;
  margin-bottom: 25px;
}

.promo__desc {
  color: #ffffff;
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;
  margin-bottom: 29px;
}

.promo__link {
  width: 100%;
}

@media (min-width: 768px) {
  .promo__container {
    position: relative;
    min-height: 807px;
    padding-left: 120px;
  }
  .promo__container img {
    position: absolute;
    bottom: 0;
    right: 28px;
  }

  .promo {
    background-color: transparent;
    text-align: left;
    background-image: none;
    padding-top: 59px;
    padding-bottom: 3px;
  }

  .promo__title {
    color: #000000;
    font-size: 60px;
    line-height: 60px;
    max-width: 450px;
    margin-bottom: 40px;
  }

  .promo__desc {
    color: #000000;
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 50px;
  }

  .promo__link {
    position: relative;
    width: 244px;
    z-index: 3;
    padding-top: 17px;
    padding-bottom: 17px;
  }
}
@media (min-width: 1440px) {
  .promo__container {
    min-height: 418px;
  }
  .promo__container img {
    right: 262px;
    left: auto;
    bottom: 0;
  }

  .promo {
    padding-top: 79px;
    padding-bottom: 132px;
  }

  .promo__desc {
    width: 400px;
  }
}
.slider__btn {
  width: 4px;
  height: 100%;
  border-radius: 2px;
  background-color: #666666;
  border: none;
  padding: 0;
  position: absolute;
  top: 0;
  left: 138px;
}
.slider__btn::before {
  content: "";
  display: block;
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #ffffff;
  border: 2px solid #666666;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}
.slider__btn svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
}
.slider__btn:hover {
  background-color: #68b738;
}
.slider__btn:hover::before {
  border-color: #68b738;
}
.slider__btn:hover svg {
  fill: #68b738;
}
.slider__btn:active {
  background-color: #5eaa2f;
  opacity: 0.6;
}
.slider__btn:active::before {
  border-color: #5eaa2f;
}
.slider__btn:active svg {
  fill: #5eaa2f;
}

.slider {
  position: relative;
  width: 280px;
  margin-left: auto;
  margin-right: auto;
}

.slider__img--fat {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  overflow: hidden;
  background-color: #eaeaea;
}
.slider__img--fat img {
  max-width: none;
}

@media (min-width: 768px) {
  .slider {
    width: 560px;
  }

  .slider__btn {
    left: 280px;
  }

  .slider__img--fat {
    background-color: transparent;
    background-image: linear-gradient(to bottom, #ffffff 151px, #eaeaea 151px);
  }

  .slider__img img {
    vertical-align: bottom;
  }
}
@media (min-width: 1440px) {
  .slider__img--fat {
    background-image: linear-gradient(to bottom, #ffffff 135px, #f2f2f2 135px);
  }

  .slider {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
.social-list {
  list-style: none;
  padding-left: 0;
  margin: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  padding-top: 20px;
  padding-bottom: 20px;
}

.social-list__link {
  width: 28px;
  height: 22px;
  display: block;
}
.social-list__link:hover svg {
  fill: #68b738;
}
.social-list__link:active svg {
  fill: #68b738;
  opacity: 0.3;
}

.social-list__item {
  margin-left: 12px;
  margin-right: 12px;
}

@media (min-width: 768px) {
  .social-list {
    border: none;
    width: auto;
    max-width: 300px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}